import { ConstructorElement, DragIcon } from '@ya.praktikum/react-developer-burger-ui-components';
import React, { FC, SyntheticEvent, useRef } from 'react';
import { ConstructorIngredientInterface } from '../../../interfaces/models/ingredient.interface';
import { useDrag, useDrop } from 'react-dnd';
import { DndIngredientType } from '../../../utils/app.types';
import styles from './burger-constructor-item.module.css';

export interface BurgerConstructorItemInterface extends ConstructorIngredientInterface {
  handleRemove?: () => void;
  moveCard: (dragIndex: number, hoverIndex: number) => void;
  index: number;
}

interface DraggableItemInterface {
  id: string;
  index: number;
}

export const BurgerConstructorItem: FC<BurgerConstructorItemInterface> = ({
  constructorId,
  name,
  price,
  index,
  image,
  handleRemove,
  moveCard
}) => {
  const ref = useRef<HTMLDivElement>(null);

  const [{ handlerId }, drop] = useDrop({
    //    ,  dnd ,
    //       ,    .
    //         
    accept: DndIngredientType.BETWEEN_BUNS,
    collect(monitor) {
      return {
        handlerId: monitor.getHandlerId()
      };
    },
    // ,      ,
    //        props.index
    hover(item, monitor) {
      if (!ref.current) {
        return;
      }
      //     
      const dragIndex = (item as DraggableItemInterface).index;
      const hoverIndex = index;
      //   ,   
      if (dragIndex === hoverIndex) {
        return;
      }
      //    
      // @ts-ignore
      const hoverBoundingRect = ref.current?.getBoundingClientRect();
      //      Y  
      //     ,  
      //     
      const hoverMiddleY = (hoverBoundingRect.bottom - hoverBoundingRect.top) / 2;
      //    ,
      //   
      const clientOffset = monitor.getClientOffset();
      //       
      //       
      // @ts-ignore
      const hoverClientY = clientOffset.y - hoverBoundingRect.top;
      //      
      //     
      //  ,       moveCard
      if (dragIndex < hoverIndex && hoverClientY < hoverMiddleY) {
        return;
      }
      //      
      //      ,    
      if (dragIndex > hoverIndex && hoverClientY > hoverMiddleY) {
        return;
      }
      //        
      moveCard(dragIndex, hoverIndex);
      //      
      //     
      (item as DraggableItemInterface).index = hoverIndex;
    }
  });
  //       
  //  
  const [{ isDragging }, drag] = useDrag({
    type: DndIngredientType.BETWEEN_BUNS,
    item: () => ({ id: constructorId, index } as DraggableItemInterface),
    collect: (monitor) => ({
      isDragging: monitor.isDragging()
    })
  });
  //     0,
  //        
  const opacity = isDragging ? 0 : 1;
  //           :)
  drag(drop(ref));
  //     onDrop
  //           
  const preventDefault = (e: SyntheticEvent) => e.preventDefault();

  const draggableItemClassName = `${styles.draggableItem}`;

  return (
    <div
      ref={ref}
      style={{ opacity }}
      onDrop={preventDefault}
      data-handler-id={handlerId}
      className={draggableItemClassName}>
      <DragIcon type="primary" />
      <ConstructorElement
        handleClose={handleRemove}
        text={name}
        price={price}
        thumbnail={image}
      />
    </div>
  );
};
